h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
  color: white;
}

body {
  font-size: rem(18);
  font-family: var(--font-barlow);
}

:root {
  --soft-red: hsl(7, 99%, 70%);
  --soft-red2: hsla(7, 60%, 53%, 0.459);
  --yellow: hsl(51, 100%, 49%);
  --yellow2: hsla(51, 89%, 56%, 0.377);
  --dark-desaturated-cyan: hsl(167, 40%, 24%);
  --dark-blue: hsl(198, 62%, 26%);
  --dark-moderate-cyan: hsl(168, 34%, 41%);
  --very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --very-dark-grayish-blue: hsl(213, 9%, 39%);
  --dark-grayish-blue: hsl(232, 10%, 55%);
  --grayish-blue: hsl(210, 4%, 67%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-barlow: "Barlow", sans-serif;
  --font-fraunces: "Fraunces", serif;
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}

.header {
  background-image: url("/images/desktop/image-header.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 90vh;
}

.header-nav {
  display: flex;
  align-items: center;
  padding: 1.2rem;
  justify-content: space-between;
}

.header-nav-nav {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.25rem;
}

.header-nav-nav li {
  margin-right: 2rem;
}

.header-nav-nav-bright {
  background-color: white;
  padding: 1rem 1.2rem;
  color: black;
  border-radius: 2rem;
  font-family: var(--font-fraunces);
  font-size: 1rem;
  transition: all 0.5s;
}

.header-nav-nav-bright:hover {
  color: white;
  background-color: rgba(255, 255, 255, 0.26);
  transition: all 0.5s;
}

.header-title {
  text-align: center;
  font-family: var(--font-fraunces);
  padding: 1.7rem;
}

.header-title h1 {
  letter-spacing: 6px;
  color: white;
}

.header-arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 3rem;
  animation: MoveUpDown 4s linear infinite;
}

@keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 40px;
  }
}
.promises {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.promise {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

#transform-text {
  grid-column: 1/7;
}

.promise-text {
  width: 60%;
  justify-self: center;
  align-self: center;
  font-weight: 600;
}

.promise-text h1 {
  font-family: var(--font-fraunces);
  font-size: 2.3rem;
}

.promise-text p {
  color: var(--very-dark-grayish-blue);
  line-height: 26px;
}

#transform-text a {
  font-family: var(--font-fraunces);
  color: black;
  padding: 0rem 0.4rem;
  border-radius: 3px;
  --d: 100%;
  background: linear-gradient(var(--yellow2) 0 0) 0 100%/var(--d, 0) 7px no-repeat;
  transition: 0.5s;
}

#transform-text a:hover {
  --d:0;
  background: linear-gradient(var(--yellow) 0 0) 0 100%/var(--d, 0) 7px no-repeat;
  transition: 0.5s;
  --d:100% ;
}

#stand-text a {
  font-family: var(--font-fraunces);
  color: black;
  padding: 0rem 0.4rem;
  border-radius: 3px;
  --d: 100%;
  background: linear-gradient(var(--soft-red2) 0 0) 0 100%/var(--d, 0) 7px no-repeat;
  transition: 0.5s;
}

#stand-text a:hover {
  --d:0;
  background: linear-gradient(var(--soft-red) 0 0) 0 100%/var(--d, 0) 7px no-repeat;
  transition: 0.5s;
  --d:100% ;
}

#transform {
  grid-column: 7/-1;
}

#stand {
  grid-column: 1/7;
}

#stand-text {
  grid-column: 7/-1;
}

.services {
  display: flex;
  justify-content: center;
  min-height: 80vh;
}

#cherry {
  background: url("/images/desktop/image-graphic-design.jpg");
}

#cherry h3 {
  color: var(--dark-desaturated-cyan);
}

#cherry p {
  color: var(--dark-moderate-cyan);
}

#orange {
  background: url("/images/desktop/image-photography.jpg");
}

#orange {
  color: var(--dark-blue);
}

.service {
  background-size: cover;
  background-repeat: no-repeat;
  flex: 1;
  position: relative;
}

.service-text {
  position: absolute;
  top: 65%;
  left: 25%;
  width: 50%;
  text-align: center;
  line-height: 27px;
}

.service-text h3 {
  font-family: var(--font-fraunces);
  font-size: 1.8rem;
}

.service-text p {
  font-size: 1rem;
}

.clients {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  margin-top: 6rem;
}

.clients-title {
  text-align: center;
  margin-bottom: 4rem;
  font-family: var(--font-fraunces);
  letter-spacing: 7px;
  color: var(--very-dark-grayish-blue);
  font-size: 1.2rem;
}

.clients-clients {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem;
}

.client {
  text-align: center;
  padding: 3rem;
  color: var(--very-dark-grayish-blue);
}

.client img {
  border-radius: 50%;
  margin-bottom: 1rem;
}

.client-info {
  margin-top: 3rem;
}

.client-info h3 {
  font-family: var(--font-fraunces);
  color: black;
}

.client-info p {
  color: var(--very-dark-grayish-blue);
}

.images {
  display: flex;
  align-items: center;
  max-width: 100vw;
}

.images-image img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.footer {
  text-align: center;
  background-color: var(--dark-moderate-cyan);
  color: var(--dark-blue);
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-title h2 {
  font-size: 2rem;
  font-weight: 900;
}

.footer-nav ul {
  display: flex;
  list-style: none;
}

.footer-nav ul li a {
  margin-right: 2rem;
  color: var(--dark-blue);
}

.footer-nav ul li:last-child {
  margin-right: 0rem;
}

.footer-links ul {
  list-style: none;
  display: flex;
}

.footer-links ul li {
  padding: 0.9rem;
}